@font-face {
    font-family: "Florentia";
    src: url('../fonts/florentia/Florentia-Regular-trial.ttf'); }

@font-face {
    font-family: "KolnMesseDeutz";
    src: url('../fonts/KolnMesseDeutz.otf'); }


$link-color: #0074E8;
$main-grey: #474749;
$lagoon: #88D1F1;
$blue-p: #CCE2FF;
$green: #92C062;
$orange: #F6AC4B;


body {
    margin: 0;
    padding: 0;
    height: 100vh;
    box-sizing: border-box;
    font-size: 16px;
    font-family: Florentia, sans-serif;
    position: relative;
    background: linear-gradient(#fff 8%, $blue-p, #fff) !important;
    background-repeat: no-repeat;
    h1 {
        text-align: center;
        font-family: NeogreyRegular, sans-serif; }
    button {
        font-family: Florentia, sans-serif !important; }
    a {
        color: $link-color;
        outline: none;
        font-family: KolnMesseDeutz, sans-serif;
        font-weight: 500; } }

.btn {
    margin-top: 10px !important; }

.row {
    padding: 0 !important; }

.grid {
    width: 100% !important;
    margin: 0 !important; }

.head {
    padding: 10px 0 !important;
    text-align: center;
    background: $link-color;
    position: "absolute";
    z-index: 2;
    a, span {
        color: $link-color;
        padding: 5px;
        font-size: 18px;
        @media(max-width: 420px) {
            font-size: 12px; } }
    .logout-link {
        cursor: pointer;
        color: $link-color;
        display: inline-block;
        margin-right: 5px;
        @media(max-width: 420px) {
            font-size: 12px; } } }


.signup-form , .login-form {
    height: 100% !important;
    margin-top: 10%; }

.dictionary {
    padding-top: 80px !important;
    td {
        word-break: break-word; }
    h1 {
        margin-bottom: 20px; }
    &__button_remove {
        cursor: pointer;
        font-size: 2em !important;
        line-height: 1.2; } }

.home-page {
    position: relative;
    &__header {
        position: absolute !important;
        z-index: 1;
        top: 40%; }
    h1 {
        font-size: 40px; }
    h3 {
        text-align: center;
        font-size: 30px; } }


.main-page {
    width: 80% !important;
    height: 100%;
    margin: auto !important;
    padding-top: 80px !important;
    @media (max-width: 1366px) {
        width: 100% !important; }
    .lang {
        @media (max-width: 767px) {
            margin-bottom: 20px !important; }
        .dropdown {
            @media (max-width: 450px) {
                min-width: 1em !important; } } }
    .main__input {
        position: relative; }
    .main__input_text {
        text-align: left;
        @media (max-width: 1200px) {
            text-align: left; }
        & input {
            padding: 30px !important;
            font-size: 2rem !important;
            width: 73% !important;
            text-align: center;
            @media (max-width: 998px) {
                font-size: 1.3rem !important;
                padding: 15px !important; }
            @media (max-width: 580px) {
                font-size: 1.3rem !important;
                padding: 15px !important;
                width: 65%; }
            @media (max-width: 400px) {
                width: 60% !important;
                font-size: 1rem !important;
                padding: 10px !important; } } }

    .main__output_translation {
        padding: 30px !important;
        font-size: 2rem !important;
        width: 80% !important;
        max-height: 330px;
        position: absolute;
        top: 150px;
        left: -5%;
        text-align: center;
        margin: auto;
        overflow: auto;
        word-wrap: break-word;
        transition: 0.3s;
        @media (max-width: 997px) {
            left: 5%;
            top: 100px; }
        .message_translated-from {
            position: absolute;
            top: 0;
            left: 40%; } }
    .main__btn_translate {
        position: absolute;
        left: 75%;
        font-size: 1.3rem;
        padding: 20px 40px;
        @media (max-width: 998px) {
            padding: 17px 26px;
            top: 0px;
            left: 73%; }
        @media (max-width: 580px) {
            padding: 17px 22px;
            left: 70%; }
        @media (max-width: 400px) {
            left: 60%;
            padding: 10px 20px;
            top: 0px;
            font-size: 1rem;
            padding: 12px 20px; } }
    .turn-lang-icon {
        line-height: 1.6 !important;
        margin: 0 5px;
        cursor: pointer; }
    .last-words {
        height: 400px;
        filter: drop-shadow(10px 10px 15px #00000099);
        @media (max-width: 767px) {
            margin-top: 200px; }
        .card {
            height: 100%;
            padding-top: 50px;
            border-radius: 5px;
            color: #FDFDFD;
            background: linear-gradient(230deg,darken(#46525E, 5%) 5%, lighten(#46525E, 10%)  25%,darken(#46525E, 5%)  100%);
            clip-path: polygon(0% 0%, 85% 0%, 100% 15%, 100% 100%, 0 100%);
            &__body {
                background: linear-gradient(230deg,lighten(#E2E2E2, 6%) 5%, #E2E2E2 25%,  lighten(#E2E2E2, 8%) 100%);
                color: #FDFDFD;
                font-weight: normal;
                margin: 10px 10px 10px 10px;
                padding: 20px;
                height: 94%;
                border-radius: 10px;
                overflow-y: auto;
                text-align: center;
                word-wrap: break-word; }
            .card__word {
                position: relative;
                border-radius: 5px;
                color: $main-grey;
                cursor: default;
                border-bottom: 1px dotted rgba(0,0,0,0.3);
                &:hover {
                    background-color: lighten($main-grey, 40%); }
                &_button {
                    position: absolute;
                    top: calc(100%/2 - 12px);
                    right: -20px;
                    padding: 0px;
                    cursor: pointer; } } } }
    .main__link_repeat {
        text-align: center;
        margin-top: 30px;
        margin-bottom: 30px;
        word-wrap: break-word;
        position: absolute;
        @media (max-width: 767px) {
            left: 25%; }
        @media (max-width: 540px) {
            left: 0%;
            font-size: 1rem; }
        @media (max-width: 350px) {
            top: 110px; } }
    .main__button-turn_wrapper {
        display: inline-block;
        height: 40px;
        position: absolute;
        z-index: 2;
        width: 120px;
        top: 3%;
        left: calc(100%/2 - 60px);
        z-index: 2;
        clip-path: polygon(10px 0%, 110px 0%, 100% 10px, 100% 30px, 110px 100%, 10px 100%, 0% 30px, 0% 10px);
        @media (max-width: 767px) {
            top: 210px;
            color: #fff; }
        .main__button-turn {
            margin-right: 0;
            width: 100%;
            height: 100%; } }
    .message_translated-from {
        position: relative;
        top: -10px;
        font-size: 0.8em;
        @media (max-width: 767px) {
            top: -25px; } } }


.repeat-page {
    h1 {
        margin-top: 50px; }
    .repeat__list {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 400px;
        .word {
            text-align: center;
            &__text {
                font-size: 20px;
                font-weight: bold;
                text-transform: uppercase; }
            &__buttons {
                margin-top: 50px;
                &_know {
                    background-color: $green; }
                &_remind {
                    background-color: $orange; } }
            &__button_next {
                margin-top: 50px;
                text-align: center;
                background-color: $green; } } } }

.empty-message {
    text-align: center;
    font-size: 30px;
    margin-top: 100px; }

